<template>
  <div>
    <div class="my-1">
      <h3>Terminos de referencia</h3>
    </div>    
    <v-data-table
      :headers="headers"
      :items="items"
      class="elevation-1"
      :items-per-page="5"
      :loading="loading"
      dense>
      <template v-slot:item.actions="{ item }">
        <v-layout justify-center my-1>
          <v-btn small @click="goto(item)" color="primary">
            <v-icon dark small left>search</v-icon>ver
          </v-btn>
        </v-layout>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import { termRef } from '@/servicios/TerminosReferencia'
export default {
  data () {
    return {
      headers: [
        { text: 'Solicitud Contratacion', align: 'center', value: 'id_solcontratacion' },
        { text: 'Proceso Contratacion', align: 'center', value: 'id' },
        { text: 'Estado', align: 'center', value: 'estado' },
        { text: 'Fecha creacion', align: 'center', value: 'fecha_creacion' },
        { text: 'Tipo Contrataci贸n', align: 'left', value: 'tipo_contratacion' },
        { text: 'Acci贸n', align: 'center', value: 'actions' }
      ],
      items: [],
      loading: false,
    }
  },
  async mounted () {
    this.getTermRefs()
  },
  methods: {
    async getTermRefs () {
      try {
        this.loading = true
        const { data } = await termRef.getTermRef({ estado: 'aprobado' })
        this.items = data
      } catch (error) {
        console.log(error)
        this.items = []
      }
      this.loading = false
    },
    goto (item) {
      console.log(item)
      this.$router.push('/Evaluacion/' + item.id)
    }
  }
}
</script>
